.section_selected {
	position: relative;

	width: 100%;
	background: linear-gradient(180deg, rgba(242, 231, 220, .4) 0%, rgba(242, 231, 220, 0) 100%);

	padding: rem(120) 0;

	@include min {
		padding: rem(80) 0;
	}

	.item_select-buffer {
		display: none;

		@include min540 {
			display: none;
			padding: 0 rem(16);
			margin-bottom: rem(16);

			.text {
				font-size: rem(14);
				line-height: rem(20);
				color: #80827F;
				margin-bottom: rem(16);
			}
		}
	}

	.item_select-buffer.active {
		display: none;

		@include min540 {
			display: block;
		}
	}

	&::after {
		content: "";
		position: absolute;
		top: 0;
		margin-top: rem(-120);
		right: 0;

		width: rem(320);
		height: rem(320);
		background: url(../img/timer.png) no-repeat top right;

		@include tablet {
			width: rem(200);
			height: rem(200);
			background-size: rem(200);
		}

		@include min {
			width: rem(147);
			height: rem(120);
			background-size: rem(120);
			margin-top: rem(-40);
		}
	}

	.text-title {
		text-align: center;
		margin-bottom: rem(80);

		@include min {
			margin-bottom: rem(40);
			padding: 0 rem(30);
		}
	}

	.text-title-50 {
		margin-bottom: rem(24);

		@include min {
			width: 100%;
		}
	}

	.item_select {
		display: flex;
		flex-wrap: wrap;
		border-radius: rem(14);
		padding-top: rem(32);
		padding-right: rem(24);
		background: linear-gradient(270deg, #FFFFFF 0%, #FFEDEF 100%);
		/* Main_Shadow */

		box-shadow: 0px 4px 8px rgba(209, 194, 194, 0.12),
		0px 16px 32px rgba(209, 194, 194, 0.08);

		width: calc(50% - 0.625rem);
		height: rem(312);
		margin-bottom: rem(62);

		@include min {
			width: 100%;
			// margin-bottom: rem(48);
			height: rem(343);
		}

		@include min540 {
			height: rem(110);
			padding-right: 0;
			padding-top: 0;
			padding-left: rem(16);
			margin-bottom: rem(16);
			justify-content: space-between;
		}

		&-left {
			position: relative;
			flex: 0 0 40%;

			@include min540 {
				order: 2;
				height: 100%;
				flex: 0 0 rem(120);
			}

			img {
				position: absolute;
				top: rem(-30);
				left: 0;
				width: 100%;

				@include min540 {
					top: -0.8rem;
					display: block;
					height: rem(122);
					object-fit: cover;
					max-width: rem(120);
				}
			}
		}

		&-right {
			flex: 0 0 60%;

			.open {
				display: none;
			}

			@include min540 {
				order: 1;

				display: flex;
				flex-direction: column;
				justify-content: center;

				.open {
					display: flex;
					align-items: center;
					font-size: rem(12);
					line-height: rem(14);
					font-weight: 600;
					color: $pink;

					i {
						margin-left: rem(8);
						font-size: rem(6);
						color: $pink;
					}
				}

				

				.smart-block {
					display: none;
				}
			}

			.title {
				font-weight: 600;
				font-size: rem(20);
				line-height: rem(30);
				margin-bottom: rem(12);
				display: block;

				@include min540 {
					font-size: rem(18);
					line-height: rem(24);
				}
			}

			.text {
				color: #80827F;
				font-size: rem(14);
				line-height: rem(20);
				margin-bottom: rem(34);
				max-width: rem(330);
			}
		}

		&-right.active {
			position: relative;
			.open {
				i {
					&::before {
						content: "\e903";
					}
				}
			}
			.smart-block {
				// position: absolute;
				display: block;
				// bottom: 0;
				// left: 0;
			}
		}
	}
}
.block_choice-wrap {
	position: relative;
	width: rem(413);

	@include min {
		width: 100%;
		order: 2;
		position: relative;
		height: rem(256);
	}

	&::before {
		content: "";
		position: absolute;
		top: rem(-93);
		left: rem(-76);
		width: rem(166);
		height: rem(146);

		background: url(../img/dumbbells.png) no-repeat;
		z-index: -1;

		@include min {
			display: none;
		}
	}
	
}
.block_choice {
	position: relative;
	
	border-radius: rem(14);
	padding: rem(67) rem(51) rem(67) rem(40);
	background: rgba(255, 255, 255, 0.8);
	/* Shadow+Blur */

	box-shadow: 0px 4px 8px rgba(209, 194, 194, 0.12),
	0px 16px 32px rgba(209, 194, 194, 0.08);
	backdrop-filter: blur(16px);
	// filter: blur(16px);

	@include min {
		position: absolute;
		top: rem(-200);
		left: 0;
		width: 100%;
	}

	@include min540 {
		top: rem(-100);
	}

	@include min540 {
		padding: rem(32) rem(18);
	}

	&::after {
		content: "";
		position: absolute;
		// top: rem(-93);
		bottom: rem(-46);
		right: rem(-52);
		width: rem(171);
		height: rem(175);

		background: url(../img/heart.png) no-repeat;
		// z-index: -1;

		@include min {
			display: none;
		}
	}

	&-item {
		display: flex;
		// flex-wrap: wrap;
		margin-bottom: rem(40);

		&:last-child {
			margin-bottom: 0;
		}

		.choice_item-image {
			flex: 0 0 rem(80);
			width: rem(80);
			height: rem(80);
			border-radius: rem(20);
			overflow: hidden;
			margin-right: rem(24);
			@include min540 {
				flex: 0 0 rem(60);
				width: rem(60);
				height: rem(60);
				margin-right: rem(16);
			}

			img {
				width: 100%;
				height: 100%;
				object-fit: cover;
			}
		}

		.choice_descr {
			flex: 0 0 1;
			.title {
				font-size: rem(24);
				line-height: rem(30);
				font-weight: 600;
				display: block;
				margin-bottom: rem(16);

				@include min540 {
					font-size: rem(18);
					line-height: rem(24);
				}
			}

		}
	}
}

.button-plus {
	display: flex;
	flex-wrap: wrap;
	align-items: center;

	line-height: rem(22);
	font-weight: 600;
	color: $pink;

	.plus {
		margin-right: rem(8);
		border-radius: 50%;
		width: rem(15);
		height: rem(15);
		border: rem(1.5) solid $pink;
		display: flex;
		align-items: center;
		justify-content: center;

		i {
			font-size: rem(9);
			font-weight: 600;
			color: $pink;
		}
	}
}

.block_choice-right {
	flex: 1 0 0;

	@include min {
		width: 100%;
		flex: 0 0 100%;
		order: 1;
	}

	img {
		width: 100%;
		height: 100%;
		object-fit: cover;
	}
}