.header {
	// position: sticky;
	// top:0;
	// z-index: 50;

	background: linear-gradient(180deg, #F2E7DC 0%, rgba(242, 231, 220, 0) 100%);

	> .container {
		padding: rem(24) 0;
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		align-items: center;
	}
		

	&_logo {
		margin-right: auto;
		a {
			max-width: rem(122);
			width: 100%;
			display: block;
		}
	}

	&_nav {
		margin-right: rem(122);

		@include min {
			display: none;
		}
		ul {
			display: flex;
			flex-wrap: wrap;
			li {
				margin-right: rem(48);
				display: flex;
				align-items: center;
				&:last-child {
					margin-right: 0;
				}
				i {
					font-size: rem(10);
					margin-left: rem(10);
				}
				a {
					font-weight: 600;
				}
			}
		}
	}

	&_right {
		

		@include min {
			display: flex;
			flex-wrap: wrap;
		}

		@include min768 {
			.button-i {
				display: none;
			}
		}
	}

	&_burger {
		display: none;

		@include min {
			margin-left: rem(24);
			display: flex;
			align-items: center;
			justify-content: center;
			width: rem(48);
			height: rem(48);
			border-radius: rem(14);
			background-color: $pink;

			.line {
				position: relative;
				width: rem(18);
				height: rem(12);
				display: flex;
				flex-direction: column;
				justify-content: space-between;

				

				&::before, &::after {
					content: "";
					width: 100%;
					background-color: white;
					height: rem(2);
					display: block;
				}
			}

			.center {
				width: 100%;
				background-color: white;
				height: rem(2);
				display: block;
			}
		}
	}
}

.menu_small {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	overflow-y: auto;
	background: linear-gradient(180deg, #F2E7DC 0%, rgba(242, 231, 220, 1) 100%);
	z-index: 100;
	padding: rem(10);

	display: none;
	flex-direction: column;


	&-header {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		align-items: center;
		margin-bottom: rem(16);

		.logo {
			max-width: rem(120);
		}

		.close {
			width: rem(32);
			height: rem(32);
			background-size: rem(32);
			background: url(../img/close.png) no-repeat;
		}
	}

	&-content {
		display: flex;
		flex-direction: column;
		
		ul {
			margin-bottom: rem(20);
			li {
				margin-bottom: rem(18);
				display: flex;
				align-items: center;

				a {
					font-size: rem(18);
					line-height: rem(24);
					font-weight: 600;
					color: $pink;
					
					
				}

				i {
					margin-left: rem(8);

					font-size: rem(10);
					color: $pink;
				}
			}
		}

		.button-i {
			margin-top: auto;
		}
	}

	&-footer {
		margin-top: auto;

		.button-i {
			.border {
				margin-left: auto;
			}
		}
	}
}

.menu_small.active {
	@include min {
		display: flex;
	}
}

.scroll_off {
	overflow: hidden;
}